import React, { useState, useEffect } from 'react';
import { get } from 'axios';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { useAuth0, withAuthenticationRequired } from "@auth0/auth0-react";
import Loading from "../components/Loading";
import Highlight from "../components/Highlight";
import configs from "../configs";
import Leaderboard from 'react-leaderboard';

export const FileUpload = () => {
  const [state, setState] = useState({
    users: [],
    paginate:20
  });

  var data=null;
  
  const { getAccessTokenSilently, getIdTokenClaims } = useAuth0();

  const handle = (e, fn) => {
    e.preventDefault();
    fn();
  };

  useEffect(() => {
    fileUpload();
  }, []);

  const fileUpload = async () => {
    try {
      const claims = await getIdTokenClaims();
      const token = claims.__raw;
      const url = configs.API_LEADERBOARD;
      const config = {
          headers: {
            'Authorization': `Bearer ${token}`,  
          }
      }
      const responseData = await get(url, config)
      setState({...state, users:responseData["data"]});
    } catch (err) {
      setState({...state, error:err});
    }
  };

  return (
    <>
      <ToastContainer />
      <div className="mb-5">
        <h1 className='primary'>Leaderboard</h1>
        <hr/>
        {state.users && (
          <div className="mt-5">
            <h4 className="muted">Leaderboard</h4>
                <Leaderboard users={state.users} paginate={state.paginate}/>
                <Highlight language="json">
                    {state.users}
                </Highlight>
          </div>
        )}
      </div>
    </>
  );
};

export default withAuthenticationRequired(FileUpload, {
  onRedirecting: () => <Loading />,
});
